import React, {ReactDOM} from 'react';
import { Layout, Header, Navigation, Drawer, Content } from 'react-mdl';
import './App.css';
import Main from './components/main'
import { Link } from 'react-router-dom'
import landingpage from './components/landingpage'

function App() {

  if (window.location.href === 'https://ericthered926.github.io/final/')
  {
    ReactDOM.render(landingpage,document.getElementsByClassName('content-layout'))
  }
  return (
    <div>
    <Layout id="override">
        <Header transparent title="Portfolio" className="header-color">
            <Navigation>
                <Link to="/" id='home'>Home</Link>
                <Link to="/aboutme" id='about'>About Me</Link>
                <Link to="/projects" id='projects'>Projects</Link>
                <Link to="/resume" id='resume'>Resume</Link>
                <Link to="/contact" id='contact'>Contact</Link>
            </Navigation>
        </Header>
        <Drawer title="Menu" id="nav-dr" className="nav-dr">
            <Navigation id="nav-dr-nav" className="nav-dr-nav">
                <Link onClick={close} to="/">Home</Link>
                <Link onClick={close} to="/aboutme">About Me</Link>
                <Link onClick={close} to="/projects">Projects</Link>
                <Link onClick={close} to="/resume">Resume</Link>
                <Link onClick={close} to="/contact">Contact</Link>
            </Navigation>
        </Drawer>
        <Content className="content-layout">
          <Main></Main>
        </Content>
    </Layout>
  </div>
  );
  
  function close() {
    console.log('fun easter egg');
    var layout = document.querySelector('.mdl-layout'); // Get layout reference
    layout.MaterialLayout.toggleDrawer();
  }
   
}





export default App;
