import React, {Component} from 'react';
import YouTube from 'react-youtube';

class AboutMe extends Component {
    componentDidMount() {
        document.title = 'Eric\'s Portfolio - About Me';
        document.getElementById('home').style.color = "white";
        document.getElementById('about').style.color = "lightblue";
        document.getElementById('projects').style.color = "white";
        document.getElementById('resume').style.color = "white";
        document.getElementById('contact').style.color = "white";
    }
    render() {
        const opts = {
            width: '50%',
            height: '450',
            playerVars: {
              // https://developers.google.com/youtube/player_parameters
              autoplay: 0,
            },
          };
        return (
            <div className='aboutme-body'>
                <h1>
                    About Me
                </h1>

                <p>My name is Eric. I'm a student studying Information Systems at BYU.
                 I attribute my passion for technology to my Redmond, Washington roots.<br/><br/>
                  I also spent some of my childhood in the San Diego area where I learned to love the outdoors 
                  and went to the beach and camping frequently. My passions include skiing, mountain biking, and 
                  running. You can find me at all the nearby concerts. I love learning new things 
                  and just want to make the world a better place. <br/><br/>Also, here's a video of me breaking my back skiing!</p>
                <YouTube videoId="6TD__YqMwZI" opts={opts} className="video-wrap" onReady={this._onReady} />
            </div>
        );
    }
}

export default AboutMe;